<template>
  <div class="order-details-page" v-if="order">
    <div class="order-details-page__button">
      <vue-yellow-button-icon
        :title="'Назад'"
        :icon="icon_back"
        @click="goToBack"
        class="component_yellow_button_icon"
      />
    </div>
    <div class="order-details-page__title h1">
      <h1>Детали заказа</h1>
    </div>
    <div class="order-details-page__details">
      <div class="h3 block-white">
        <h3>Задача: {{ order.title }}</h3>
      </div>
      <div class="order-details-page__details__description h3 block-white">
        <h3>Описание:</h3>
        <p>{{ order.description }}</p>
      </div>
      <div class="order-details-page__details__address block-white">
        Адрес: <span>{{ order.location }}</span>
      </div>
      <div class="order-details-page__details__time block-white">
        Начало работы в <span>{{ order.time }}</span>
      </div>
      <div class="order-details-page__details__worker-time block-white">
        <p>
          Заказ рассчитан на
          <vue-formatted-minutes :minutes="order.workingHours" />
        </p>
      </div>
      <div class="order-details-page__details__taxi block-white">
        <p>
          Заказчик
          <span :class="{ yes: order.taxi }">{{
            order.taxi ? "готов" : "не готов"
          }}</span>
          оплатить такси
        </p>
      </div>
      <div class="order-details-page__people block-white">
        <p>
          Собрано
          <span :class="{ wait: order.activePeople < order.maxPeople }">{{
            order.activePeople
          }}</span>
          из
          <span>{{ order.maxPeople }}</span>
        </p>
      </div>
      <div class="order-details-page__details__payment-info">
        <vue-yellow-button
          class="component_yellow_button"
          :title="'Взять заказ'"
          @click="takeAnOrder"
        />
        <p>
          Цена: <span>{{ order.priceInfo.price }}руб.</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import VueYellowButtonIcon from "@/components/Buttons/VueYellowButtonIcon.vue";
import VueYellowButton from "@/components/Buttons/VueYellowButton.vue";
import VueFormattedMinutes from "@/components/Formatted/VueFormattedMinutes.vue";
import { useRoute, useRouter } from "vue-router";
import cardsData from "@/localDatabase/cards.json";
import { onMounted, ref } from "vue";
import icon_back from "@/assets/icons/back-arrow.svg";
import { inject } from "vue";
import { Order } from "../types/CardOrder";

const userRole = inject("userRole");

const route = useRoute();
const router = useRouter();

let order = ref<Order | null>(null);

onMounted(async () => {
  const orderId = await Number(route.params.id);

  order.value = cardsData.find((item) => {
    return item.id === orderId;
  }) as Order;
});

const goToBack = () => {
  router.back();
};
const takeAnOrder = () => {
  if (!userRole) {
    router.push("/registration");
  }
};
</script>

